import Vue from 'vue'

export const loadData = ({commit}) => {
    Vue.http.get('/star-brands-portal/public/api/brands/read')
        .then(response => response.json())
        .then(data => {
            const brands = data
            commit('SET_BRANDS', brands)
        })
}
export const loadCategorys = ({commit}, g) => {
    Vue.http.get(`/star-brands-portal/public/api/brands/${g}/categories/read`)
        .then(response => response.json())
        .then(data => {
            const categories = data
            commit('SET_CATEGORIES', categories)
        })
}
export const loadProducts = ({commit}, g) => {
    Vue.http.get(`/star-brands-portal/public/api/categories/${g}/products/read`)
        .then(response => response.json())
        .then(data => {
            const products = data
            console.log(products)
            commit('SET_PRODUCTS', products)
        })
}



