<template>
<div class="row">
  <div class="col-sm-12">






<div class="row">
  <div class="col-sm-6 col-md-4 img" v-for="product in allProducts" v-bind:key="product.id">
    <div class="thumbnail" v-if="product.multi">

  
        <app-carousel :sliders="product.assets" ></app-carousel>



        
      <!-- <img v-bind:src="'/star-brands-portal/public'+product.assets[0].thumb" alt="..."> -->
        <div class="caption">
            <h3>{{ product.name }}</h3>
            <center><p><a v-bind:download="product.description+'.jpg'" v-bind:href="'/star-brands-portal/public'+product.assets[0].file" class="btn btn-primary" role="button">Download  <span class="glyphicon glyphicon-download" aria-hidden="true"></span></a> </p></center>
      </div>
    </div>
    <div class="thumbnail" v-else>

  
      <img v-bind:src="'/star-brands-portal/public'+product.assets[0].thumb" alt="...">
       




        
      <!-- <img v-bind:src="'/star-brands-portal/public'+product.assets[0].thumb" alt="..."> -->
        <div class="caption">
            <h3>{{ product.name }}</h3>
           <center> <p><a v-bind:download="product.description+'.jpg'" v-bind:href="'/star-brands-portal/public'+product.assets[0].file" class="btn btn-primary" role="button">Download  <span class="glyphicon glyphicon-download" aria-hidden="true"></span></a>
</p></center>
      </div>
    </div>
  </div>
</div>
      
        <!-- <div class="col-sm-4 img" v-for="product in allProducts" v-bind:key="product.id">
            <img v-bind:src="'/star-brands-portal/public'+product.assets[0].thumb" class="img-responsive">

            {{ product.name }}
        </div> -->

</div>
  </div>
</template>

<script>
import Carousel from './Carousel.vue'
export default {
  data(){
    return{
      showthisBrand: '',
      sliders: []
    }
  },
  components: {    
    brands:[],
    appCarousel: Carousel 
  },
  watch: {
      '$route.params.categoryId': function (cateforyId) {
      this.getData()
      }
  },
  created: function () {
    // this.$store.dispatch("loadData", { self: this })
    // this.$store.dispatch("loadCategorys", this.$route.params.id, { self: this })
    this.getData()
  },
    methods: {
          getData() {

       this.$store.dispatch("loadProducts", this.$route.params.categoryId, { self: this })
    //   })
  },
  brandClicked(e){
    this.showthisBrand = e;
  }
    },
  computed: {
    allProducts() {
      return this.$store.getters.allProducts      
    }

  }
}
</script>

<style lang="scss" scoped>
.img{
    .thumbnail{
height: 300px;
img{
    max-height: 150px;
}
    }
    
 
}
</style>



